/*Base Style*/
$cover-main : rgba(135, 177, 173, 0.9);
$cover-back: rgba(183, 225, 185, 0.9);
html {
   box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz--box-sizing: border-box;
  -ms--box-sizing: border-box;
  background: linear-gradient($cover-main, $cover-back);
  height: 100%;
  *, *::after, *::before{
    margin: 0;
    padding: 0; 
    box-sizing: inherit;
    outline: 0;
  }
}